import React from 'react';
import GLink from 'gatsby-link';

import styles from './blog-index.module.scss';

const IndexPage = ({ data }) => {
  const { edges } = data.allMarkdownRemark;

  return (
    <div>
      {
        edges
          .filter(edge => edge.node.frontmatter.title.length > 0)
          .map(edge => (
            <div className={styles.postListing}>
              <span className={styles.postTitle}><GLink to={edge.node.frontmatter.path}>{edge.node.frontmatter.title}</GLink></span>
              &nbsp;â€¢&nbsp;
              <span className={styles.postDate}>{edge.node.frontmatter.date}</span>
              <div className={styles.excerpt}>&ldquo;{edge.node.excerpt}&rdquo;</div>
            </div>
          ))
      }
    </div>
  );
};

export default IndexPage;

export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 200)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
          }
        }
      }
    }
  }
`;



